export const SET_LANG = 'SET_LANG'
const store = {
    namespaced: true,
    state: {
        lang: localStorage.getItem('lang')
    },
    actions: {
        setLang({commit}, lang) {
            commit('SET_LANG', lang)
        }
    },
    mutations: {
        [SET_LANG] (state, data) {
            state.lang  = data
        }
    }
}
export default store